import React from 'react';
import './Timer.scss';
import { Button } from 'antd';
// import { DownloadOutlined } from '@ant-design/icons';

export default class Timer extends React.Component {

  state = {
    minutes: 0,
    seconds: 0,
    milliseconds: 0,
    active: false,
  }

  startTimer = () => {
    const timer = () => {
      const callback = () => {
        if (this.state.active) {
          this.setState((state) => {
            if (this.state.seconds > 59) {
              return {
                seconds: 0,
                minutes: state.minutes + 1,
              }
            }
            return {
              seconds: state.milliseconds > 999 ? state.seconds + 1 : state.seconds,
              milliseconds: state.milliseconds > 999 ? 0 : state.milliseconds + 25,
              minutes: state.minutes > 59 ? 0 : state.minutes,
            }
          });
          timer();
        }
        clearTimeout(millisecondsId);
      }
      const millisecondsId = setTimeout(callback, 25);
    }
    this.setState((state) => ({ active: !state.active, }));
    timer();
  }

  resetTimer = () => {
    this.setState({
      minutes: 0,
      seconds: 0,
      milliseconds: 0,
      active: false,
    })
  }

  render() {
    const { minutes, seconds, milliseconds } = this.state;
    const minute = minutes < 10 ? `0${minutes}` : minutes;
    const second = seconds < 10 ? `0${seconds}` : seconds;
    const millisecond = milliseconds < 10 ? `0${milliseconds}` : milliseconds;
    seconds
    return (
      <div className="tab-timer">
        <div className="tab-timer__timer">{`${minute} : ${second} :`}<span className="tab-timer__ms">{millisecond}</span></div>
        <div className="tab-timer__wrap-buttons">
          <Button
            className="tab-timer__start-button"
            onClick={this.startTimer}
            type="primary"
            shape="round"
            icon={"Запустить/Пауза"}
            size="large"
          />
          <Button
            className="tab-timer__reset-button"
            onClick={this.resetTimer}
            type="primary" shape="round"
            icon={"Сбросить"}
            size="large"
          />
        </div>
      </div>
    )
  }
}